import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store'
import ElementUi from 'element-ui' // ElementUi
import 'element-ui/lib/theme-default/index.css'
import axios from 'axios' // axios
import {fastclick} from './assets/js/common/index'
fastclick(document.body) // fastclickclickTap()300s

import { Picker } from 'mint-ui' //npm install mint-ui --save
import { Popup } from 'mint-ui'
import 'mint-ui/lib/style.css'
Vue.component(Picker.name, Picker)
Vue.component(Popup.name, Popup)

import './assets/js/common/config'
import './assets/js/lib/flexible' // jsmeta

window.axios = axios

//let AUTH_TOKEN = 'amVzc2lldGFuZyswOTI1' // base64 jessietang+0925 (+) AUTH_TOKEN
//axios.defaults.headers['Content-Type'] = 'application/x-www-form-urlencoded'
axios.defaults.baseURL = rootSrc.api; // config.js
//axios.defaults.headers.common['Authorization'] = AUTH_TOKEN

Vue.use(ElementUi)

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  template: '<App/>',
  components: { App }
})



