const config = () => {
  let rootSrc;
  if (process.env.NODE_ENV !== 'production') { //
    rootSrc = {
      //api: 'http://125.71.30.136:8706/'
      api: 'http://125.71.30.136/AppWebApi'
    }
  } else { //
    rootSrc = {
      api:`http://125.71.30.136/AppWebApi`
    }
  }
  window.rootSrc = rootSrc
}

config()



